import Result from './Result'

/**
 * 
 * @param {Object} 
 * @param {Object} 
 * @returns {Object} 
 * :
 * const rules = {
 *   name: {
 *     alias: 'key',                   -------- key
 *     required: 'true()',      -------- true
 *     pattern: //,                      -------- 
 *     validate: '(12)'
 *                                                      --------  true false ""
 *     message: ''                    -------- 
 *     trim: @Boolean,true                          --------- 
 *   },
 *   mobile: { ... }
 * }
 * :
 * const result = { name: '', mobile: '' };
 * result.hasError  // 
 * result.first()       //     ------ 
 * result.firstKey()    // key   ------ 
 */

export function validator (target, rules) {
  const ruleKeys = rules ? Object.keys(rules) : []
  if (!ruleKeys.length) return new Result()
  const results = ruleKeys.reduce((errors, key) => {
    let value = target[key]
    let tips = null
    const { required, pattern, validate, alias = key, message = `${alias}`, trim = true } = rules[key] || {}
    // 
    trim && typeof value === 'string' && (value = value.trim())
    if (typeof value === 'undefined' || value === null || !value.toString().length || JSON.stringify(value) === '{}') {
      required && (tips = typeof required === 'string' ? required : `${alias}`)
    } else if (pattern && pattern instanceof RegExp && !pattern.test(value)) { // 
      tips = message
    } else if (typeof validate === 'function') { // 
      const res = validate(value, target)
      tips = typeof res === 'string' ? res : (!res ? message : null)
    }
    return tips ? { ...errors, [key]: tips } : { ...errors }
  }, {})
  return new Result(results)
}



