
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Scroll from 'assembly/scroll/scroll'
import Uploader from 'assembly/uploader/uploader'
import localStore from 'util/localStore'
import { validateNotNull } from 'util/rules'
import { weeklyRules, monthlyRules } from './rules'
import { validator } from './validator'

export default {
data () {
return {
file: '',
baseUrl: process.env.FMS_SERVICE_IP,
weeklyFrom: {}, // 
monthlyFrom: {}, // 
storeInfo: {}, // 
type: 0,
ifStoreManager: false,
storeManagerNo: false,
ifIdentity: false,
ifWeChat: false,
fileList: [],
managerMenuNo: {
true: '1',
false: '0'
},
managerMenu: {
true: '',
false: ''
},
identityMenu: {
true: '',
false: ''
},
maskShow: false,
status: 0,
statusText: ['', ''],
readonly: false,
weekStatus: '', // /
weekText: '',
monthText: '',
inited: false 
}
},
watch: {
'weeklyFrom': {
handler (val, oldVal) {

this.weeklyFrom.totalSalesAmts = Number(val.counterSaleAmts || 0) + Number(val.lijiaSalesAmts || 0) +
Number(val.grpbSalesAmts || 0) + Number(val.customisedSalesAmts || 0) + Number(val.othersSalesAmts || 0) + Number(val.onlinesaleamt || 0)

this.weeklyFrom.totalCustomerNo = this.weeklyFrom.naturalCustomerNo + this.weeklyFrom.comebackCustomerNo +
this.weeklyFrom.customisedCustomerNo + this.weeklyFrom.lijiaCustomerNo

this.weeklyFrom.totalPrsttTimes = Number(val.customisedPrsttTimes || 0) + Number(val.lijiaPrsttTimes || 0) +
Number(val.imdtlyPrsttTimes || 0) + Number(val.waterPurifierSalesAmts || 0)

this.weeklyFrom.totalOrderAmt = Number(val.comOrderAmt || 0) + Number(val.transferAmt || 0)
},
deep: true
},
'monthlyFrom': {
handler (val, oldVal) { // 
this.monthlyFrom.operationCharge = (Number(val.decorationCharge / 36 || 0) + Number(val.laborCharge || 0) + Number(val.roomCharge || 0) +
Number(val.lightWaterCharge || 0) + Number(val.promoCharge || 0) + Number(val.phoneCharge || 0) + Number(val.logisticalCharge || 0) +
Number(val.taxCharge || 0) + Number(val.othersCharge || 0) + Number(val.psmCharge || 0) + Number(val.warehouseCharge || 0)).toFixed(4)
},
deep: true
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
onError () {
this.$vux.toast.text('~')
},
onFinish (res, content, file) {
this.file = { url: res.model, name: file.name }
},
toHistory () {
this.$router.push({
path: '/weekReportList'
})
},
changeStatus (index) {
if (index === this.status) {
return
}
if (index === 0) { // 
this.$tupHttp(this, 'front/report/opening').then(res => {
this.status = index
this.readonly = false
})
}
if (index === 1) { // 
this.$tupHttp(this, 'front/report/shutdown').then(res => {
this.status = index
this.readonly = true
})
}
},
showMask () {
this.maskShow = true
},
clickMask () {
this.maskShow = false
},
showManagerNoSelector () {
if (!this.readonly) {
this.storeManagerNo = true
}
},
showManagerSelector () {
if (!this.readonly) {
this.ifStoreManager = true
}
},
showIdentitySelector () {
if (!this.readonly) {
this.ifIdentity = true
}
},
showWeChatSelector () {
this.ifWeChat = true
},
setManagerNo (key, item) {
this.weeklyFrom.managerNo = key === 'true' ? 1 : 0
},
setManager (key, item) {
this.weeklyFrom.isManager = key === 'true' ? 1 : 0
},
setIdentity (key, item) {
this.weeklyFrom.managerType = item
},
setWeChat (key, item) {
this.monthlyFrom.hasWechat = key === 'true' ? 1 : 0
},
changeType (type) {
if (type === this.type) {
return
}
if (type === 1 && !this.weeklyFrom.id) {
this.$vux.toast.text('~')
return
}
this.type = type
},
next () {
if (this.checkWeeklyError()) {
return
}
this.type = 1
},
toSubmitWeekly () {
if (this.inited) {
this.$vux.toast.text('~')
return
}
if (this.weekStatus !== 3) {
delete this.weeklyFrom.weekendStockAmt 
delete weeklyRules.weekendStockAmt 
}
if (this.file) {
this.weeklyFrom.groupContractName = this.file.name
this.weeklyFrom.groupContractUrl = this.file.url
} else {
this.weeklyFrom.groupContractName = ''
this.weeklyFrom.groupContractUrl = ''
}
if (this.checkWeeklyError()) {
return
}
this.inited = true
this.$tupHttp(this, 'front/report/weekly-submit', 'POST', this.weeklyFrom).then(res => {
this.inited = false
this.$vux.toast.text('~')
this.setWeekDetail(res.data.model)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
toSubmitMonthly () {
if (this.inited) {
this.$vux.toast.text('~')
return
}
if (localStore.get('TUP_pInfo').pCitoutletproperty2 === '8796121170011') {
if (!this.monthlyFrom.psmCharge || this.monthlyFrom.psmCharge < 0) {
this.$message.error('-PSM>=0')
return
}
if (!this.monthlyFrom.warehouseCharge || this.monthlyFrom.warehouseCharge < 0) {
this.$message.error('->=0')
return
}
}
const result = validator(this.monthlyFrom, monthlyRules)
if (result.hasError()) {
this.$vux.toast.text(result.first())
return
}
this.inited = true
this.$tupHttp(this, 'front/report/monthly-submit', 'POST', this.monthlyFrom).then(res => {
this.inited = false
this.$vux.toast.text('~')
this.setMonthDetail(res.data.model)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
checkWeeklyError () {
const result1 = validator(this.weeklyFrom, weeklyRules)
if (result1.hasError()) {
this.$vux.toast.text(result1.first())
return true
}
if (this.weeklyFrom.othersSalesAmts && this.weeklyFrom.othersSalesAmts > 1000000) {
this.$vux.toast.text('-100')
return true
}
if (this.weeklyFrom.grpbSalesAmts > 20000 && !this.file) {
this.$vux.toast.text('-20000')
return true
}





if ((this.weeklyFrom.counterSaleAmts < 5001 ||
this.weeklyFrom.counterSaleAmts > 50000) ||
this.weeklyFrom.lijiaSalesAmts > 20000 ||
this.weeklyFrom.customisedSalesAmts > 20000 ||
this.weeklyFrom.onlinesaleamt > 20000 ||
this.weeklyFrom.naturalCustomerNo > 120 ||
this.weeklyFrom.comebackCustomerNo > 50 ||
this.weeklyFrom.lijiaCustomerNo > 20 ||
this.weeklyFrom.customisedCustomerNo > 15 ||
this.weeklyFrom.lijiaPrsttTimes > 12 ||
this.weeklyFrom.imdtlyPrsttTimes > 40 ||
this.weeklyFrom.dealAmts > 50) {
if (!this.weeklyFrom.reportDesc || this.weeklyFrom.reportDesc.length < 4 || this.weeklyFrom.reportDesc.length > 199) {
this.$vux.toast.text('4-200')
return true
}
}
/* if (!(this.validateNoAndAmt('waterPurifierSalesAmts', 'waterPurifierSalesAmount', '') && this.validateNoAndAmt('slimBeautySetNo', 'slimBeautySetAmts', '') &&
this.validateNoAndAmt('sskitSaleNo', 'sskitSaleAmts', '43') && this.validateNoAndAmt('wqpanSaleNo', 'wqpanSaleAmts', '5.7L') &&
this.validateNoAndAmt('slevelPanSaleNo', 'slevelPanSaleAmts', '') && this.validateNoAndAmt('cupSaleNo', 'cupSaleAmount', '') &&
this.validateNoAndAmt('thermosSaleNo', 'thermosSaleAmount', ''))) {
return
} */
if (Number(this.weeklyFrom.managerNo) === 0) {
this.weeklyFrom.managerName = ''
this.weeklyFrom.isManager = ''
this.weeklyFrom.managerType = ''
} else if (Number(this.weeklyFrom.managerNo) === 1) {
if (!validateNotNull(this, this.weeklyFrom.isManager, '-')) {
return true
} else if (this.weeklyFrom.isManager === 1 && !validateNotNull(this, this.weeklyFrom.managerName, '-')) {
return true
} else if (this.weeklyFrom.isManager === 1 && !validateNotNull(this, this.weeklyFrom.managerType, '-')) {
return true
}
}
return false
},
toSubmitAll () {
if (this.inited) {
this.$vux.toast.text('~')
return
}
if (this.file) {
this.weeklyFrom.groupContractName = this.file.name
this.weeklyFrom.groupContractUrl = this.file.url
} else {
this.weeklyFrom.groupContractName = ''
this.weeklyFrom.groupContractUrl = ''
}
if (this.checkWeeklyError()) {
return
}
if (localStore.get('TUP_pInfo').pCitoutletproperty2 === '8796121170011') {
if (!this.monthlyFrom.psmCharge || this.monthlyFrom.psmCharge < 0) {
this.$vux.toast.text('-PSM>=0')
return
}
if (!this.monthlyFrom.warehouseCharge || this.monthlyFrom.warehouseCharge < 0) {
this.$vux.toast.text('->=0')
return
}
}
const result = validator(this.monthlyFrom, monthlyRules)
if (result.hasError()) {
this.$vux.toast.text(result.first())
return
}
this.inited = true
this.$tupHttp(this, 'front/report/merge-submit', 'POST', { monthly: this.monthlyFrom, weekly: this.weeklyFrom }).then(res => {
this.inited = false
this.$vux.toast.text('~')
this.monthId = res.data.model.id
this.setMonthDetail(res.data.model)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
setWeekDetail (data) {
if (data.groupContractUrl) {
this.file = { name: data.groupContractName, url: data.groupContractUrl }
}
this.weeklyFrom = {
id: data.id,
counterSaleAmts: data.counterSaleAmts,
lijiaSalesAmts: data.lijiaSalesAmts,
grpbSalesAmts: data.grpbSalesAmts,
customisedSalesAmts: data.customisedSalesAmts,
othersSalesAmts: data.othersSalesAmts,
onlinesaleamt: data.onlinesaleamt,
dealAmts: data.dealAmts || 0,
breakevenpoint: data.breakevenpoint,
totalSalesAmts: data.totalSalesAmts,
waterPurifierSalesAmts: data.waterPurifierSalesAmts,
waterPurifierSalesAmount: data.waterPurifierSalesAmount,
slimBeautySetNo: data.slimBeautySetNo,
slimBeautySetAmts: data.slimBeautySetAmts,
sskitSaleNo: data.sskitSaleNo,
sskitSaleAmts: data.sskitSaleAmts,
wqpanSaleNo: data.wqpanSaleNo,
wqpanSaleAmts: data.wqpanSaleAmts,
slevelPanSaleNo: data.slevelPanSaleNo,
slevelPanSaleAmts: data.slevelPanSaleAmts,
cupSaleNo: data.cupSaleNo,
cupSaleAmount: data.cupSaleAmount,
thermosSaleNo: data.thermosSaleNo,
zzylgNo: data.zzylgNo,
xyjydbNo: data.xyjydbNo,
thermosSaleAmount: data.thermosSaleAmount,
naturalCustomerNo: data.naturalCustomerNo,
comebackCustomerNo: data.comebackCustomerNo,
customisedCustomerNo: data.customisedCustomerNo,
lijiaCustomerNo: data.lijiaCustomerNo,
totalCustomerNo: data.totalCustomerNo,

lijiaPrsttTimes: data.lijiaPrsttTimes,
imdtlyPrsttTimes: data.imdtlyPrsttTimes,
totalPrsttTimes: data.totalPrsttTimes,
comOrderAmt: data.comOrderAmt,
transferAmt: data.transferAmt,
lijiaFulltimeConsutantNo: data.lijiaFulltimeConsutantNo,
returnToWorkConsutantNo: data.returnToWorkConsutantNo,
weekendStockAmt: data.weekendStockAmt,
totalOrderAmt: data.totalOrderAmt,
managerNo: data.managerNo,
isManager: data.isManager || data.isManager === null ? 1 : data.isManager === false ? 0 : data.isManager,
managerName: data.managerName,
managerType: data.managerType,
reportDesc: data.reportDesc,
groupContractUrl: data.groupContractUrl,
groupContractName: data.groupContractName
}
},
setMonthDetail (data) {
this.monthlyFrom = {
id: data.id,
decorationCharge: data.decorationCharge,
laborCharge: data.laborCharge,
roomCharge: data.roomCharge,
lightWaterCharge: data.lightWaterCharge,
promoCharge: data.promoCharge,
phoneCharge: data.phoneCharge,
logisticalCharge: data.logisticalCharge,
taxCharge: data.taxCharge,
othersCharge: data.othersCharge,
psmCharge: data.psmCharge,
warehouseCharge: data.warehouseCharge,

operationCharge: data.operationCharge,
hasWechat: data.hasWechat ? 1 : data.hasWechat === false ? 0 : data.hasWechat,
wechatGrpNo: data.wechatGrpNo,
wechatMembersNo: data.wechatMembersNo,
wechatMomentsMembersNo: data.wechatMomentsMembersNo
}
},
_getWeekDetail () {
this.$tupHttp(this, 'front/report/weekly-detail').then(res => {
let data = res.data.model
this.weekText = data.year + '/' + data.month + ' ' + data.week + ''
this.setWeekDetail(data)
})
},
_getMonthDetail () {
this.$tupHttp(this, 'front/report/monthly-detail').then(res => {
let data = res.data.model
this.monthText = data.year + '/' + data.month
this.setMonthDetail(data)
})
},
_getStatus () {
this.$tupHttp(this, 'front/report/myStatus').then(res => {
this.weekStatus = res.data.model
this.storeInfo = res.data.extra
this.status = this.storeInfo.pOutletStatus === '8796118941787' ? 0 : 1
this.readonly = this.status === 1
this.storeInfo.pOutletStatus = this.storeInfo.pOutletStatus === '8796118941787' ? '' : ''
if (this.weekStatus === 2 || this.weekStatus === 3) {
this._getWeekDetail()
}
if (this.weekStatus === 3) {
this._getMonthDetail()
}
})
}
},
created () {
this._getStatus()
},
components: {
Group,
XButton,
XHeader,
XNumber,
XInput,
PopupPicker,
Cell,
Uploader,
Actionsheet,
XTextarea,
Scroll
}
}



